
/* Table styling */
table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 30px;
  background-color: #ffffff;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  overflow: hidden;
}

/* Header */
thead {
  background-color:#4fd0f7;
}

th {
  padding: 12px;
  text-align: left;
  font-weight: 750;
  color: #333;
  border-bottom: 2px solid #e0e0e0;
}

input{
  padding: 3px 20px;
  margin-left: 50px;
  margin-top: 8px;
  border: 1px solid #12e9f8;
  border-radius: 10%;
  background-color: #ffe3e3;
}

.bt-submit{
  margin-top: 6px;
  margin-left: 43%;
  color: #ffffff;
  background-color: #ff0ade;
  border-radius: 10% ;
  padding: 6px 12px;
  border: none;
  cursor: pointer;
  font-size: 14px;
  
}

.bt-submit:hover{
  background-color: #ff0998;
  transition: background-color 0.3s;

}

/* Rows */
td {
  padding: 8px 16px;
  font-weight: 500;
  border-bottom: 1px solid #eee;
  color: #555;
}

tr:hover {
  background-color: #bcc6ff;
  transition: background-color 0.3s;
}

/* Buttons */



.action-buttons button {
  margin-right: 15px;
  padding: 8px 14px;
  border: none;
  color: rgb(247, 244, 244);
  cursor: pointer;
  border-radius: 4px;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.action-buttons .edit-btn {
  background-color: #1976d2;
}

.action-buttons .edit-btn:hover {
  background-color: #301af7;
}

.action-buttons .delete-btn {
  background-color: #d32f2f;
}

.action-buttons .delete-btn:hover {
  background-color: #ff1313;
}




